const mutations = {
    increment(state) {
        state.count++
    },
    // 设置用户信息
    SET_userInfo(state, result) {
        // console.log('进入设置用户mutatios')
        state.userInfo = result
        // console.log(state.userInfo)
    },
    // 设置课程列表
    SET_SyllabusList(state, result) {
        state.SyllabusList = result
    },
    // 设置课程详细信息
    SET_DetailSyllabus(state, result) {
        state.DetailSyllabus = result
    },
    // 设置当前校历周
    SET_SchoolTime(state, result) {
        state.SchoolTime = result
    },
    // 设置当前校历周次列表
    SET_TimeList(state, result) {
        state.TimeList = result
    }
}
export default mutations



