  // 将jsAPI转换为VUE插件
  import ITfer from './JSAPI/index'

  function getITfer() {
      return new ITfer()
  }
  let install = function(Vue) {
      Object.defineProperties(Vue.prototype, {
          $cjc: {
              get: function get() {
                  return getITfer()
              }
          }
      })
  }
  export default install



