<template>
    <div id="app">
        <router-view :oldRouter="oldRouter"></router-view>
    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            oldRouter: null,
            newRouter: null
        }
    },
    watch: {
        // 监听路由是否从二级页面返回
        $route(val) {
            this.oldRouter = this.newRouter
            this.newRouter = val.name
        }
    }
}
</script>
<style lang=less>
@size-color:#495060;
body {
    background-color: #f8f8f8;
}
#app {
    height: 100vh;
    background-color: #f8f8f8;
    padding-top: 50px;
}
</style>




