import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const home = resolve => {
    require.ensure(['../Pages/home.vue'], () => {
        resolve(require('../Pages/home.vue'))
    })
}
const details = resolve => {
    require.ensure(['../Pages/details.vue'], () => {
        resolve(require('../Pages/details.vue'))
    })
}

let routes = [
    {
        path: '/',
        redirect: '/home'
    },
    {
        path: '/home',
        name: 'home',
        component: home
    },
    {
        path: '/details',
        name: 'details',
        component: details
    }
]
let router = new Router({
    mode: 'history',
    routes
})
router.beforeEach((to, from, next) => {
    if (from.path === '/') {
        sessionStorage.setItem('key_id', to.query.key_id)
        sessionStorage.setItem('type', to.query.type)
    }
    next()
})
export default router




