
import api from './fetch/api.js'
const getHost = () => {
    return 'http://tfapp.tfswufe.edu.cn'
    // return '..'
}
const actions = {
  /**
   * 获取登录信息actions
   * @param {*} params username password type 1学生 2教职工
   */
    userlogin({commit}, params) {
        console.log('进入获取登录信息actions')
        return new Promise((resolve, reject) => {
            api.minePostMsgApi('http://micapi.tfswufe.edu.cn' + '/itfer/rest/user/login', params)
            .then(res => {
                resolve(res)
            })
        })
    },
    async getUserInfo({commit}, params) {
        return await api.minePostMsgApi(`http://micapi.tfswufe.edu.cn/itfer/rest/user/getUserInfo`, params)
    },
    /**
     * 确认用户信息
     * @param {*} param0
     * @param {*} params 用户信息
     */
    async saveUserInfo({commit}, params) {
        return await api.minePostMsgApi(getHost() + '/itfer-kbcx/rest/lookSyllabus/saveUserInfo', params)
    },
    /**
     * 获取课程列表
     * @param {*} param0
     * @param {*} params
     */
    async getSyllabusList({commit}, params) {
        return await api.mineBaseMsgApi(getHost() + '/itfer-kbcx/rest/lookSyllabus/getSyllabusList/' + params.xh + '/' + params.xn + '/' + params.xq + '/' + params.zc)
    },
    /**
     * 获取课程详细信息
     * @param {*} param0
     * @param {*} params
     */
    async getDetailSyllabus({commit}, params) {
        return await api.mineBaseMsgApi(getHost() + '/itfer-kbcx/rest/lookSyllabus/getDetailSyllabus/' + params.xh + '/' + params.xn + '/' + params.xq + '/' + params.kch)
    },
    /**
     * 获取当前校历周
     * @param {*} param0
     * @param {*} params
     */
    async getSchoolTime({commit}, params) {
        return await api.mineBaseMsgApi(getHost() + '/itfer-kbcx/rest/lookSyllabus/getSchoolTime')
    },
    /**
     * 获取当前校历周次
     * @param {*} param0
     * @param {*} params
     */
    async getTimeList({commit}, params) {
        return await api.mineBaseMsgApi(getHost() + '/itfer-kbcx/rest/lookSyllabus/getTimeList')
    }
}

export default actions



